import { useEffect, useState } from "react";
import Clock from "./components/Clock";
import ClockDial from "./components/ClockDial";
import Timer from "./components/Timer";
import "./styles.css";

export default function App() {
  const [now, setNow] = useState(new Date());
  const [circles, setCircles] = useState({ hours: 0, minutes: 0, seconds: 0 });

  const date = {
    hours: now.getHours(),
    minutes: now.getMinutes(),
    seconds: now.getSeconds()
  };

  useEffect(() => {
    setInterval(() => {
      const newDate = new Date();

      setNow(newDate);
      setCircles((state) => {
        return {
          hours: state.hours + (newDate.getHours() === 0 ? 1 : 0),
          minutes:
            state.minutes +
            (!newDate.getMinutes() && !newDate.getSeconds() ? 1 : 0),
          seconds: state.seconds + (!newDate.getSeconds() ? 1 : 0)
        };
      });
    }, 1000);
  }, []);

  return (
    <div>
      <ClockDial date={date} circles={circles} />
      <Clock date={date} />
      <Timer now={now} />
    </div>
  );
}
