import styles from "./ClockDial.module.css";

const ClockDial = (props) => {
  const { hours, minutes, seconds } = props.date;

  const hours_angle =
    hours * (360 / 12) + (30 / 60) * minutes + 360 * props.circles.hours;
  const minutes_angle =
    minutes * (360 / 60) + (6 / 60) * seconds + 360 * props.circles.minutes;
  const seconds_angle = seconds * (360 / 60) + 360 * props.circles.seconds;

  let points = [];
  for (let i = 1; i <= 12; i++) {
    points.push(
      <div
        key={i}
        className={styles.point}
        style={{ transform: `rotate(${i * 30}deg)` }}
      >
        <div className={styles.mark} />
        <div
          className={styles.number}
          style={{ transform: `translateX(-50%) rotate(${-i * 30}deg) ` }}
        >
          {i}
        </div>
      </div>
    );
  }

  return (
    <div className={styles.dial}>
      {points}
      <div
        className={styles.hours}
        style={{ transform: `rotate(${hours_angle}deg)` }}
      />
      <div
        className={styles.minutes}
        style={{ transform: `rotate(${minutes_angle}deg)` }}
      />
      <div
        className={styles.seconds}
        style={{ transform: `rotate(${seconds_angle}deg)` }}
      />
      <div className={styles.circle} />
    </div>
  );
};

export default ClockDial;
