export function calculateTime(date) {
  const today = date.getDay();

  const end = new Date(date.getTime());

  if (today === 4) {
    end.setHours(16);
    end.setMinutes(12);
  } else {
    end.setHours(17);
    end.setMinutes(0);
  }
  end.setSeconds(0);

  const dif = end.getTime() - date.getTime();

  const dif_hour = Math.floor((dif % 86400000) / 3600000);

  const dif_minutes = Math.floor((dif % 3600000) / 60000);

  const dif_seconds = Math.floor((dif % 60000) / 1000);

  return { hours: dif_hour, minutes: dif_minutes, seconds: dif_seconds };
}
