import styles from "./Clock.module.css";

const Clock = (props) => {
  const { hours, minutes, seconds } = props.date;

  const formated_hours = hours < 10 ? `0${hours}` : `${hours}`;
  const formated_minutes = minutes < 10 ? `0${minutes}` : `${minutes}`;
  const formated_seconds = seconds < 10 ? `0${seconds}` : `${seconds}`;

  return (
    <div className={styles.clock}>
      {formated_hours}:{formated_minutes}:{formated_seconds}
    </div>
  );
};

export default Clock;
