import styles from "./Timer.module.css";

import { calculateTime } from "../helpers/function";

export default function Timer(props) {
  const { hours, minutes, seconds } = calculateTime(props.now);

  if (seconds < 0) return <div>День закончился</div>;

  return (
    <div className={styles.timer}>
      До конца дня осталось
      <br /> {!hours ? "" : `${hours} часов`}{" "}
      {!hours && !minutes ? "" : `${minutes} минут`} {`${seconds} секунд`}
    </div>
  );
}
